.products {
  .category-carousel__wrapper {
    height: 470px;
    position: relative;

    > div {
      padding: 20px 25px;
      padding-left: 40px;
      outline: none;
      // height: 470px;
    }
  }

  .prod-carousel-item {
    max-width: 310px;
    width: 100%;
    height: 420px;
    padding: 25px;

    &:hover {
      box-shadow: 0px 0px 15px 3px #aaaaaa;
    }
  }

  .prod-carousel-item__img {
    width: 100%;
    height: 260px;
    overflow: hidden;

    @media ($down: $md) {
      height: 125px;
    }
  }

  .prod-carousel-item__name {
    font-size: 22px;
    font-weight: 500;
    color: #242424;
  }

  .prod-carousel-item__piece {
    font-size: 16px;
    line-height: 20px;
    color: #666;
    font-weight: 500;
  }

  .prod-carousel-item__price {
    font-size: 28px;
    color: $red;
    margin-top: 10px;
    font-weight: 600;
  }

  .carousel-arrow {
    display: block;
    width: 30px;
    height: 40px;
    background: none;
    background-position: center;
    background-repeat: no-repeat;
    background-size: auto;
    border: none;
    outline: none;
    cursor: pointer;
    position: absolute;
    z-index: 10;

    &:hover {
      opacity: 0.7;
    }

    &.arrow-back {
      left: -3px;
      top: 0;
      height: 95%;

      @media ($down: $md) {
        top: -50px;
      }
    }

    &.arrow-next {
      right: -3px;
      top: 0;
      height: 95%;

      @media ($down: $md) {
        top: -50px;
      }
    }

    &:disabled {
      opacity: 0.5;
    }
  }

  .carousel__slide-focus-ring {
    outline: none;
  }

  .carousel__dots-wrapper {
    display: flex;
    justify-content: center;
    margin-bottom: 50px;

    @media ($down: $md) {
      flex-wrap: wrap;
    }
  }

  .carousel__dot {
    display: flex;
    padding: 20px 40px;
    border: none;
    border-bottom: 1px solid #fff;
    background: none;
    color: #fff;
    font-size: 26px;
    font-weight: 500;
    text-transform: capitalize;
    outline: none;

    @media screen and (max-width: 1040px) {
      padding: 20px 20px;
      font-size: 20px;
    }
    @media ($down: $sm) {
      padding: 15px;
    }
  }

  .carousel__dot--selected {
    border-bottom: 4px solid #fff;
  }

  .carousel__dot {
    border-bottom: 1px solid #242424;
    color: #242424;
  }

  .carousel__dot--selected {
    border-bottom: 4px solid $red;
  }
}

.products-service__content {
  padding: 140px 0;
  background: rgba(0, 0, 0, 0.7);
  height: 100%;

  @media ($down: $md) {
    padding: 80px 0;
  }
  @media ($down: $sm) {
    padding: 50px 0;

    > div {
      flex-direction: column;
    }
  }
}

.service-box {
  text-align: center;
  padding: 0 20px;

  > div {
    margin-bottom: 25px;

    @media ($down: $sm) {
      margin-bottom: 15px;
    }
  }

  img {
    width: 95px;
    height: 95px;

    @media ($down: $sm) {
      width: 75px;
      height: 75px;
    }
  }

  @media ($down: $sm) {
    margin-bottom: 30px;
  }
}

// MOBILE DISPLAY NONE UNTIL FIX STYLE
.products-page-carousel {
  @media ($down: $sm) {
    display: none;
  }
}
